.video {
  display: flex;
  justify-content: center;
  content: "";
  margin: 0 auto 150px;
  width: 100%;
  height: 246px;

  @include OnTablet {
    height: 400px;
  }

  @include OnDesktop {
    height: 590px;
  }

  background-image: url(../images/image4.svg);
  background-position: center;
  background-size: contain;
  background-repeat: no-repeat;
}

.about-us {
  margin: 0 30px;

  @include OnTablet {
    margin: 0;
  }

  &__img {
    grid-row: 1/1;

    background-image: url(../images/image3.svg);
    background-position: center;
    background-repeat: no-repeat;
    background-size: contain;

    height: 210px;

    @include OnTablet {
      height: 415px;
      background-size: cover;
      background-position: right;
      grid-row: 1/6;
    }

    @include OnDesktop {
      height: 388px;
    }
  }

  &__title {
    grid-row: 2/2;

    font-family: "DIN Pro", sans-serif;
    font-size: 37px;
    line-height: 55px;
    color: $second-color;

    @include OnTablet {
      font-size: 56px;
      line-height: 72px;
      grid-row: 1/2;
    }
  }

  &__description {
    grid-row: 3/3;
    color: $color-text;

    @include OnTablet {
      font-size: 16px;
      line-height: 24px;
      grid-row: 3/3;
    }
  }

  &__more {
    grid-row: 4/4;

    font-size: 12px;
    line-height: 15px;
    text-transform: uppercase;
    transition: width 0.3s ease;

    margin-bottom: 92px;

    &::after {
      content: "";
      display: block;
      width: 53px;
      height: 1px;
      background-color: #0db2b3;
      transition: width 0.3s ease;
    }

    &:hover::after {
      width: 120px;
    }

    @include OnTablet {
      margin-bottom: 130px;
      grid-row: 4/4;
    }

    @include OnDesktop {
      margin-bottom: 150px;
    }
  }

  &__link {
    text-decoration: none;
    box-sizing: border-box;
    color: #0db2b3;
  }
}
