.specs {
  width: fit-content;
  margin: 0 30px 90px;

  @include OnTablet {
    width: unset;
  }

  &__img {
    height: 46px;
    width: 58px;
    display: flex;
    margin: 0 auto;
    margin-bottom: 10px;

    transition: transform 0.3s ease;

    &:hover {
      transform: scale(1.2);
    }

    &--tablet {
      display: none;

      @include OnTablet {
        display: block;
        margin-bottom: 18px;
      }
    }
  }

  &__title {
    font-family: "DIN Pro", sans-serif;
    font-size: 24px;
    line-height: 30px;
    text-align: center;
    text-transform: uppercase;

    margin-bottom: 20px;
    justify-content: center;

    @include OnTablet {
      margin-bottom: -10px;
    }

    &--tablet {
      display: none;

      @include OnTablet {
        display: block;
      }
    }
  }

  &__description {
    margin-bottom: 32px;
    text-align: center;
    color: $color-text;

    &--tablet {
      display: none;

      @include OnTablet {
        display: block;
      }
    }
  }

  &__grid--row {
    &-1-1 {
      grid-row: 1/1;
    }

    &-1-2 {
      grid-row: 2/2;
    }

    &-3-1 {
      grid-row: 4/4;

      @include OnDesktop {
        grid-row: 1/1;
      }
    }

    &-3-2 {
      grid-row: 5/5;

      @include OnDesktop {
        grid-row: 2/2;
      }
    }

    @include OnDesktop {
      &-2-1 {
        grid-row: 1/1;
      }

      &-2-2 {
        grid-row: 2/2;
      }
    }
  }
}
